#about{
    display: flex;
    flex-direction: column;
    align-items: center;
}
#about img{
    max-width: 200px;
    max-height: 200px;
    border-radius: 50%;
    object-fit: cover;
    align-self: center;
}
@media only screen and (min-width:768px){
    #about{
        flex-direction: row;
    }
   
}
